*{
    list-style: none;
}
.logo{
    font-family: 'Josefin Sans', sans-serif;
}
a:hover{
    text-decoration: none;
}
.owl-dots{
    display: none;
} 
.owl-prev{
    padding: 4px;
    background: #ccc;
}
.brands{
    display:'block';
    margin-top:'-7px';
    margin-left:'17px';
    font-size:'32px';
}
.me{
    color: #2bccff;
    text-decoration: underline;
}
.me:hover{
    color: #50a3bd;
}
.grad{
    background-image: -webkit-linear-gradient(135deg, #3bade3 0%, #576fe6 25%, #9844b7 51%, #ff357f 100%);
    background-image: linear-gradient(-45deg, #3bade3 0%, #576fe6 25%, #9844b7 51%, #ff357f 100%);
}
body{
    background-image: -webkit-linear-gradient(120deg, #a6c0fe 0%, #f68084 100%);
    background-image: linear-gradient(120deg, #a6c0fe 0%, #f68084 100%);
}
.rounded{
    border-radius:'15px';
}
.rope:hover{
    background: linear-gradient(120deg, #a6c0fe 0%, #f68084 100%);
}
.foot-about{
    display: grid;
    grid-template-columns: 1fr 1fr;
}
span.font-weight-bold.brands{
    display:'block';
    margin-top:'-7px';
    margin-left:'17px';
    font-size:'32px';
}
/* Media Sizes */
@media only screen and (min-width: 400px){
    .foot-about{
        display: block;
        padding: 8px;
        font-size: 16px;
    }
}
@media only screen and (min-width: 760px){
    .copyrights{
        margin-left: -130px;
        font-size: 11px;
    }
    .foot-about{
        display: grid;
        grid-template-columns: 1fr 1fr;
        padding: 18px;
        grid-gap: 18px;
    }
}
@media only screen and (min-width:1024px){
    .copyrights{
        font-size: 18px;
    }
    .foot-contact{
        display:'grid';
        grid-template-columns:'9fr 1fr';
        grid-gap:'55px';
    }
}